@import "smart-grid";
@import "vars";
.reset();
.wrapper{
  .wrapper();
}
body{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: @color_body;
}
h2{
  font-family: 'Pacifico', cursive;
  font-size: 24px;
}

//Header
header{
  background: @bg_header;
  padding: 40px 0;
  .items{
    .row-flex();
    justify-content: space-between;
    align-items: center;
    .header(position, relative);
  }
  .headerBlock-block({
  position: static;
  display: block;

  })
}
.address__contact{
  .headerBlock-block({
  display: block;
  text-align: justify;
  .col();
  })
}
.price{
  .header-block({
  position: absolute;
  right:  0;
  top: 0;
  });
  .headerBlock-block({
  position: static;


  })
}
.products-active{
  border: 1px solid @color_body;
  transform: scale(1.1);

}

.logo,.search-form,.price{
  .col();
}
.search-form{
 .header-block({
 display: block;
 width: 100%;
 margin: 0 auto;
 text-align: center;

 })

}
.logo {
  transition: color 0.5s;
  display: flex;
  align-items: center;
  &:hover{
    color: #0053A0;
  }

  &__img img {
    width: 70px;
    height: 85px;

  }

}
.logo__tittle--1, .logo__tittle--2{
  display: block;
}

.logo__tittle--1{
  font-family: 'Pacifico', cursive;
  font-size: 24px;
}
.search-form__form{
  margin-top: 5px;


}
.search-form__search,.search-form__btn{
  vertical-align: middle;
}
.search-form__search{
  width: 525px;
  padding: 10px 0;
  .search-block({
  .size(7);
  });
  .headerBlock-block({
  .size(10);
  margin-left: -15px;
  margin-bottom: 10px;


  })

}

.icon-search{
  height: 25px;
  width: 30px;
  vertical-align: middle;

}





.icon-address{
  vertical-align: middle;
}
.price{
  border: 1px solid #A9A9A9;
  padding: 8px;

}

.price__row{
  display:flex;
  align-items: center;
}


.line{
  height: 48px;
  width: 1px;
  background: #A9A9A9;
  display: inline-block;
  margin: 0 20px;
}
.icon-buy1{
  height: 45px;
  width: 45px;

}
//end Header

//Nav_menu
nav{
  background: @bg_nav;
  padding: 10px 0;

}
.nav__items{
  .row-flex();
  .menu-block({
  display: none;

  })

}
.nav__item{
  .menu-block({
    display: block;

  });


  &:first-child{
    margin-left: 15px;
    .menu-block({
      display: block;
      margin: 0;

    });

  }


}
.nav__link{

  .menu-block({
  border: none;
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid @color_body;
  &:hover{
    background: #FF444C;
    color: #ffffff;
  }

  });
  text-decoration: none;
  color: @color_body;
  border-left: 1px solid @color_body;
  border-right: 1px solid @color_body;
  padding: 10px 60px;
  font-weight: 800;
  transition: all 0.4s;
  display: block;
  &:first-child{
    border-left: none;

  }
  &:hover{
    background: #FFFFFF;
    color: #0053A0;
  }


}
.menu-trigger{
  text-align: center;
  display: none;
  text-decoration: none;
  color: @color_body;
  font-weight: 800;
  .menu-block({
  display: block;
  })
}
.icon-nav{
  width: 45px;
  height: 40px;

}
//Aside

.icon-menu{
  width: 20px;
  height: 20px;
  vertical-align: bottom;
  margin-right: 10px;
}

.left-side__category{
  font-family: 'Pacifico', cursive;
  font-size: 21px;
  background: @bg_nav;
  border: 1px solid @color_body;
  padding-left: 20px;

}

.left-side__link{
  display: block;
  text-decoration: none;
  color: @color_body;
  padding: 10px 0;
  background: @bg_header;
  border: 1px solid @color_body;
  transition: all 0.4s;
  padding-left: 20px;
  &:hover{
    background: #FFFFFF;
    color: #0053A0;
  }
  &:focus{
    color: #F2BD36;
  }

}
.banner{
  margin: 20px 0;
}
.banner__img{
  display: block;

}
//end aside
//main
main{
  padding: 40px 0;
}
.left-side{
  flex-basis: 300px;

}
.main-row{
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  justify-content: space-between;
}
.left-side, .collection{
  .col();
}
.collection{
  flex-grow: 1;

}
.real{
  position: relative;
  min-height: 320px;

  .row-flex();
  justify-content: space-between;

  &__photo,&__tittle{
    .col();
  }
  &__photo{
    position: relative;

    flex-basis: 300px;

  }
  &__img{
    position: absolute;
    display: block;
    opacity: 0;
    transition: opacity 1s;

  }

  &__tittle{
    flex: 1 1;

  }

  &__h1{
    font-family: 'Poppins', sans-serif;
    font-size: 40px;
  }
  &__h2{
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
  }
  &__btn{
    background: #DAE805;
    padding: 10px 30px;
    text-transform: uppercase;
    color: black;
    border: 1px solid #7EBFD9;
    margin: 10px 0;
    cursor: pointer;
    border-radius: 10px;
    margin-top:20px;

  }
  .showed{
    opacity: 1;
  }

//  Slider
  .icon-arrows{
    width: 50px;
    height: 50px;


  }
  .arrs-left{
    left: 0;
    position: absolute;
    top: 50px;

  }
  .arrs-right{
    right: 0;
    position: absolute;
    top: 50px;

  }

}
.ads__row{
  .row-flex();
  justify-content: space-between;

}
.ads__item{
  .col();
  .size(3);
  border: 1px solid @color_body;
  background: #65BEB4;
  color: #26272B;
  padding: 5px 0;
  border-radius: 10px;

  &:hover{
    transform: scale(1.1);

  }



}
.ads__tittle{
  text-decoration: none;
  display: block;
  text-transform: uppercase;
  color: #26272B;


}
.ads__text{
  font-size: 14px;
  display: block;
  vertical-align: middle;
  margin-left: 5px;

}

.ads__tittle{
  font-size: 18px;
  font-weight: 800;
  color: #232428;
  display: block;

}

.ads__icon{
  width: 25px;
  height: 25px;
  vertical-align:bottom;
  margin: 0 7px ;
}


//section
.items-section{
  .row-flex();
}
.products{
  .col();
  .size(3);
  .size-products(6);
  .size-productsSm(12);
  text-align: center;
  transition: color 0.4s;
  cursor: pointer;
  &:hover .products__price{
    color: #DF222A;
  }


}
.products__btn{
  display: block;
  text-decoration: none;
  background: @color_btn;
  color: black;
  padding: 8px 0;
}
.buy-2{
  height: 20px;
  width: 20px;
  vertical-align: bottom;
  padding: 0 8px;
}

//footer
footer{
  background: @bg_header;
  padding: 40px 0;
}
.footer-items{
  .row-flex();
  justify-content: space-between;
  align-items:flex-start;

}
.logo--footer,.item-footer{
  .col();
  .size(3);
  .size-footer(6);
  .size-foorerSm(12);

}
.item-footer{
  transition: color 0.5s;
  &:hover .item-footer__name{
    color: #0053A0;
  }
}
.item-footer__name{
  border-bottom: 2px solid black;

}

.item-footer__link{
  text-decoration: none;
  display: block;
  color: @color_body;
  font-size: 14px;
  padding: 5px 0;
  transition: color 0.3s;
  &:hover{
    color: #ffffff;
  }
}

small{
  padding: 8px 0;
  display: block;
  background: black;
  text-align: center;
  color: #ffffff;
}

